* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  background-color: #F2F2F2;
}

$menu-width: 96px;
.nopadding {
  padding: 0 !important;
  margin: 0 !important;

}

.entry {
  background-color: #F2F2F2;

  &__menu {
    position: fixed;
    width: $menu-width;
    height: 100vh;
    background: #FFFFFF;
    border-radius: 0px 20px 20px 0px;


    &__logo {
      text-align: center;
      margin-top: 40px;
      margin-bottom: 30px;
      cursor: pointer;

    }

    &__line {
      text-align: center;
    }

    &__icons {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      cursor: pointer;

      &__img {
        width: 100%;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: center;

        &--setting-icon {
          position: absolute;
          bottom: 0;
        }

        &:hover {
          background-color: #F2F2F2;
        }
      }


    }

  }

  &__content {

    margin-left: $menu-width;


    &__search-and-icons {
      height: 100px;
      display: flex;
      justify-content: space-between;

      &__search {

        padding-left: 40px;
        flex: 1;
        padding-top: 26px;

        &__wrap {
          display: flex;
          align-items: center;

          img {
            height: 18px;
            width: 18px;
          }

          input {
            width: 100%;
            background: none;
            border: none;
            outline: none;

          }
        }

      }

      &__icons {
        display: flex;
        align-items: center;
        padding-right: 40px;

        &__icon {
          position: relative;

          cursor: pointer;

          &:hover {

            transform: translate(0px, -4px);

          }

          &__badge {
            position: relative;
            top: 12px;
            right:18px;

            &--notvisible {
              visibility: hidden;
            }
          }

          &__plus {
            position: absolute;
            top: 10px;
            left: 10px;


          }
        }
      }


    }

    &__main {
      margin-right: 40px;
      margin-left: 40px;


      &__chart {
        height: 505px;
        background: blue;
        background: #FFFFFF;
        border: 1px solid #000000;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 10px;


      }

      &__bars {

        height: 505px;
        background: red;

        margin-left: 40px;
        background: #FFFFFF;
        border-radius: 10px;

      }

      &__people {

        height: 320px;


        &__person {
          background: #FFFFFF;
          border-radius: 10px;
          padding-left: 20px;
          padding-top: 18px;
          padding-bottom: 18px;
          margin-bottom: 40px;
          display: flex;
          justify-content: space-between;
          &__left {
            display: flex;
            max-width: 163px;

            img {
              height: 44px;
              width: 44px;
            }

            &__info {
              padding-left: 8px;

              h1 {
                white-space: nowrap;
                font-family: sans-serif;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 20px;
                margin-bottom: 0px;

              }

              h2 {
                white-space: nowrap;
                font-family: sans-serif;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 20px;
                margin-bottom: 0px;
                color: gray;
              }
            }
          }
          &__right{
            width: 355px;
            max-width: 355px;
            margin-right: 30px;
            margin-left: 27px;
            display: flex;
            flex-direction: column;
            position: relative;
            h5{
              align-self: flex-end;
              font-size:14px;


            }
            &__bar-bg{

              height: 8px;


              border-radius: 71.5px;

              background: #DAD7FE;
              border-radius: 71.5px;

            }
            &__bar{
              height: 8px;
              width: 173px;

              border-radius: 71.5px;

              background: #4339F2;
              border-radius: 71.5px;
              position: relative;
              top:-8px;
            }
          }

        }


      }

      &__oval-bar {

        height: 320px;
        background: orange;
        margin-left: 40px;

        background: #FFFFFF;
        border-radius: 10px;
        &__top{
          height: 40px;
          padding-top:20px;
          display: flex;
          justify-content: space-between;
          img{
            height: 24px;
            width: 24px;
            margin-right: 30px;
            &:hover{
              cursor: pointer;
            }
          }
          h5{
            margin-left: 30px;
            font-family: sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
          }
        }
        &__mid{
          height: 195px;
          width: 195px;

          margin: 23px auto 22px;
        }
        &__bottom{
          height: 40px;
          padding-left: 30px;
          display: flex;
          align-items: center;
          img{
            height: 14px;
            width: 14px;


          }
          h5{
            font-family: sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 0px;
            margin-left: 6px;
          }
        }
      }

      &__right {
        height: 320px;
        margin-left: 40px;
        background: #FFFFFF;
        border-radius: 10px;
        position: relative;
        display: flex;
        justify-content: space-between;
        z-index: 0;
        &__info{
          margin-left: 30px;
          margin-top: 20px;
          h1{
            font-size: 40px;
          }
          h2{
            font-size: 20px;
          }
          h3{
            font-size: 14px;
            color: gray;
          }
        }
        &__icon{
          margin-top: 18px;
          margin-right: 30px;
          z-index: 100;
          img{

            height: 24px;
            width:24px;

          }
          &:hover{
            cursor: pointer;
          }

        }
        &>img{
          width: 100%;
          position: absolute;
          bottom:0px;
          object-fit: contain;
          z-index: 10;
        }


      }
    }
  }
}
